* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
background-color: rgb(41, 40, 40);
  
}
.main{
  background-color: black !important;
}
.startGame{
  max-width: 500px;
  height: 550px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items:center;
  

}
.startGame h1{
  margin-top: 50px;
  margin-bottom: 20px;

}

.startGameButton{
  margin-top: 10px;
  padding: 10px 30px;
  font-weight: 700;
  font-size: 16px;
}

.quiz, .quiz-content{
  width: 550px;
  height: 100%;
}
.quiz{
  background-image: url("../image/its_quiz_time.jpg");
}
.quiz-content{
  padding: 50px 40px;
  background-image: url("../image/background1.webp");
  background-size: cover;
  background-position: center;
  
 
}

ul{
  list-style-type: none;
}

.question{
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
  list-style: none; 
  margin: 40px, 75px;
  line-height: 19px;
}

.all-answers{
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  padding: 5px 10px;
  margin-right: 10px;
  margin-top: 5px;
  border-radius: 10px;
  border: none;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  
}
.all-answers:hover{
  cursor: pointer;
}
.button-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
button{
  margin: 10px 20px;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.line{
  color: black;
  line-height: 2px;
  margin: 15px 0;
}
@media screen and (min-width: 500px ){
  .quiz, .quiz-content{
   margin: 20px auto;
  }
}
@media screen and (max-width: 800px ){
  .quiz, .quiz-content{
    margin: 20px auto;
    max-width: 700px;
   
  }
}
@media screen and (min-width: 801px ){
  .quiz, .quiz-content{
   margin: 50px auto;
  
  }
  .quiz-content{
    width: 700px;
  }
}
@media screen and (max-width: 1440px ){
  .quiz, .quiz-content{
    margin: 50px auto;
    max-width: 1200px;
   
  }
}


